import mapConfig from '../../lviv.map.data.json';

const defaultCity = mapConfig[mapConfig.cities[0]];
export const centerCoords = defaultCity.center;
export const centerZoom = defaultCity.zoom;
const companies = defaultCity.companies;

export const generateMarkers = () =>
  companies.map(company => ({
    id: company._id,
    name: company.name,
    url: company.url,
    lat: company.location && company.location.lat,
    lng: company.location && company.location.lng,
    address: (company.location && company.location.q)
      ? decodeURIComponent(company.location.q.split('/')[company.location.q.split('/').length - 1])
      : '~'
  }));



